import React from 'react';
import './techStack.css';

const TechStack = () => {
  return (
      <div class="container" style={{height: '70%'}}>
        <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 row-cols-lg-4 row-cols-xl-5">
          <div class="col mb-4">
              <div className="progress blue">
                   <span className="progress-left">
                      <span className="progress-bar"></span>
                    </span>
                    <span className="progress-right">
                       <span className="progress-bar"></span>
                    </span>
                  <div className="progress-value">PYTHON</div>
              </div>
          </div>
          <div class="col mb-4">
              <div className="progress blue">
                   <span className="progress-left">
                      <span className="progress-bar"></span>
                    </span>
                    <span className="progress-right">
                       <span className="progress-bar"></span>
                    </span>
                  <div className="progress-value">Java</div>
              </div>
          </div>
          <div class="col mb-4">
              <div className="progress blue">
                   <span className="progress-left">
                      <span className="progress-bar"></span>
                    </span>
                    <span className="progress-right">
                       <span className="progress-bar"></span>
                    </span>
                  <div className="progress-value">Flutter</div>
              </div>
          </div>
          <div class="col mb-4">
              <div className="progress blue">
                   <span className="progress-left">
                      <span className="progress-bar"></span>
                    </span>
                    <span className="progress-right">
                       <span className="progress-bar"></span>
                    </span>
                  <div className="progress-value">JavaScript</div>
              </div>
          </div>
          <div class="col mb-4">
              <div className="progress blue">
                   <span className="progress-left">
                      <span className="progress-bar"></span>
                    </span>
                    <span className="progress-right">
                       <span className="progress-bar"></span>
                    </span>
                  <div className="progress-value">TypeScript</div>
              </div>
          </div>
          <div class="col mb-4">
              <div className="progress blue">
                   <span className="progress-left">
                      <span className="progress-bar"></span>
                    </span>
                    <span className="progress-right">
                       <span className="progress-bar"></span>
                    </span>
                  <div className="progress-value">Groovy</div>
              </div>
          </div>
          <div class="col mb-4">
              <div className="progress blue">
                   <span className="progress-left">
                      <span className="progress-bar"></span>
                    </span>
                    <span className="progress-right">
                       <span className="progress-bar"></span>
                    </span>
                  <div className="progress-value">90%</div>
              </div>
          </div>
        </div>
      </div>
  )
}

export default TechStack


