import React from "react";
import { Link, withRouter } from "react-router-dom";
import './../index.css';

const NavBar = () => {
    return(
        <nav className="navbar fixed-top navbar-expand-lg navbar-dark" style={{ backgroundColor: "#00796B" }}>
        <div className="container">
          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarResponsive">
            <ul className="navbar-nav justify-content-center ml-auto">
              <li className="nav-item">
                <Link className="nav-link" to='/'>Me</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/techStack">TechStack</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/experience">Experience</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/projects">Projects </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    )
}

export default withRouter(NavBar);